import React from 'react'


const checkboxStyle = {
	display: 'inline-block',
	textAlign: 'center',
	width: '35px'
}


export const Checkbox = ({ checked, onClick }) => {
	return (
		<span style={checkboxStyle} className='pull-left'>
			<input type='checkbox' checked={checked} onClick={onClick} />
		</span>
	)
}
